@use "mixins" as *;
@use "font" as *;
@use "container" as *;

.generic {
	display: flex;
	flex-direction: column;
	gap: gap-scale(1);
	@extend %container-fluid;

	&__header {
		&--flex {
			display: flex;
		}

		:is(h1, h2, h3, h4, h5, h6) {
			color: var(--color-primary);
			display: inline;
			margin-right: 0.5ch;
		}

		&__extra:has(.help, button, .icon-buttons) {
			margin-top: 0;
			display: inline-flex;
		}

		&__extra:has(button) {
			vertical-align: sub;
			--icon-size: 32;
		}

		.help,
		.icon-buttons {
			vertical-align: baseline;
		}

		.icon-buttons {
			display: inline-flex;
		}

		&__extra {
			@extend %font-1;
			color: var(--color-text1);
			margin-top: 8px;
			flex-grow: 1;
		}

		a {
			@include hoverType1;
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		gap: inherit;
	}
}