@use "button" as *;
@use "mixins" as *;
@use "icon" as *;
@use "font" as *;
@use "container" as *;
@use "text" as *;
@use "breakpoint" as *;

.chat {
  flex-grow: 1;
  display: grid;

  &__container {
    @extend %container-fluid;
  }

  &__inner {
    display: grid;
    height: 100%;
    position: relative;
    grid-template-columns: 1fr;

    @media (min-width: $mobile) {
      grid-template-columns: minmax(min-content, 320px) 1fr;
    }
  }

  &__aside {
    border-right: var(--default-border);
    padding: gap-scale(1) gap-scale(0.5) gap-scale(1) 0;
    display: flex;
    flex-direction: column;
    gap: 24px;
    height: 0;
    min-height: 100%;

    @media (max-width: $mobile) {
      background-color: var(--color-text-alt);
      z-index: 1;
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: max(50%, 240px);
      transition: transform ease var(--default-duration);
      transform: translateX(0);
      box-shadow: 4px 0 4px 0 rgba(34, 60, 80, 0.2);
    }

    &:not(.is-active) {
      @media (max-width: $mobile) {
        transform: translateX(calc(-100% - var(--container-padding)));
      }
    }
  }

  &__create {
    @extend %button;
    max-width: unset;
    width: 100%;

    &::after {
      content: "";
      @extend %icon, %icon--add;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: var(--list-gap);
    --list-gap: 16px;
    overflow: auto;

    &__group {
      display: flex;
      flex-direction: column;
      gap: 4px;

      &:not(:first-child) {
        padding-top: var(--list-gap);
        border-top: var(--default-border-width) dashed var(--default-border-color);
      }

      &__title {
        @extend %font-4;
      }
    }

    &__item {
      display: flex;
      align-items: center;
      padding: 0 8px;
      gap: 8px;

      &__title {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 0;
        flex-grow: 1;
        padding: 6px 0;
        @include hoverType2;

        @media (max-width: $mobile) {
          text-overflow: clip;
        }
      }

      &:not(:hover) &__menu:not(.is-active) {
        @media (min-width: $mobile) {
          width: 0;
          overflow: hidden;
        }
      }

      &__menu {
        display: flex;
        position: relative;

        &__button {
          border-radius: var(--default-border-radius-small);
          color: var(--color-primary);

          &:hover,
          &.is-active {
            background-color: var(--color-primary);
            color: var(--color-text-alt);
          }

          &::after {
            content: "";
            @extend %icon, %icon--more_h;
          }
        }

        &__item {
          color: var(--color-primary);
          background-color: #0000;
          border-radius: var(--default-border-radius-small);
          padding: 4px;

          &--danger {
            color: var(--color-accent-alt1);
          }

          &:hover {
            background-color: color-mix(in srgb, currentColor 10%, #0000);
          }
        }

        &:not(.is-active) &__popover {
          display: none;
        }

        &__popover {
          display: flex;
          gap: 4px;
          position: absolute;
          bottom: calc(100% + 8px);
          right: 0;
          box-shadow: 0 0 8px 0 rgba(34, 60, 80, 0.2);
          width: max-content;
          background: var(--color-text-alt);
          padding: 4px;
          border-radius: var(--default-border-radius-small);
        }
      }
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: gap-scale(1);
    padding: gap-scale(1) 0 gap-scale(1) gap-scale(1);

    @media (max-width: $mobile) {
      padding: gap-scale(1) 0;
      justify-content: space-between;
    }
  }

  &__welcome {
    text-align: center;
    @extend %h2;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 12px gap-scale(0.5);

    &__controls {
      display: flex;
      justify-content: space-between;
      gap: inherit;

      @media (max-width: $mobile) {
        display: contents;
      }

      &__variants {
        display: flex;
        gap: inherit;
        align-items: center;
        margin-right: auto;
      }

      &__buttons {
        display: flex;
        align-items: inherit;
        gap: inherit;
      }
    }

    &__counter {
      color: color-mix(in srgb, var(--color-text-alt) 50%, #0000);
      align-self: center;

      span:first-child {
        display: inline-block;
        text-align: right;
        width: calc(1ch * var(--maxlength));
      }

      &.is-error {
        color: var(--color-accent-alt1);
      }
    }

    &__button {
      @extend %button;
      --icon-size: 24;

      @media (max-width: $mobile) {
        max-width: unset;
        flex-grow: 1;
      }

      .loader {
        --loader-size: var(--icon-size);
        --loader-color: currentColor;
      }
    }

    .select-simple {
      height: var(--button-height);

      @media (max-width: $mobile) {
        width: 0;
        min-width: 100%;
        max-width: 100%;
      }

      &__select {
        height: 100%;
      }

      select {
        height: 100%;
      }
    }

    .input {
      textarea {
        resize: none;
      }
    }
  }

  &__title {
    @extend %h3;
  }

  &__action {
    @include hoverType2;

    @media (max-width: $mobile) {
      --icon-size: 26;
      padding: 7px;
    }
  }

  &__dialog {
    --dialog-gap: #{gap-scale(0.75)};
    display: flex;
    flex-direction: column-reverse;
    gap: var(--dialog-gap);
    flex-grow: 0;
    height: 0;
    overflow: auto;
    position: relative;
    transition: flex-grow ease-out var(--default-duration);

    &.is-active {
      flex-grow: 1;
    }

    @media (min-width: $mobile) {
      padding: 0 gap-scale(0.5) 0 0;
    }

    &__message {
      &:not(&--chat) {
        max-width: min(90%, 700px);
        align-self: flex-end;
      }

      &:not(&--chat) &__text {
        padding: 12px;
        background-color: var(--color-primary);
        border-radius: var(--default-border-radius);
        color: var(--color-text-alt);
      }

      &__text {
        @extend %text;

        hr {
          border: none;
          height: 1px;
          background-color: var(--color-primary);
        }
      }

      &:not(:hover) &__actions {
        @media (min-width: $mobile) {
          opacity: 0;
        }
      }

      &__actions {
        display: flex;
        gap: gap-scale(0.5);
        transition: opacity ease var(--default-duration);
        padding: 0 8px;
        margin-top: gap-scale(0.5);

        @media (max-width: $mobile) {
          padding: 0;
          margin-top: 0;
        }
      }

      &:not(&--chat) &__actions {
        justify-content: flex-end;
      }

      .labels {
        display: inline-flex;
        margin-left: 0.5ch;
      }
    }

    &__goto {
      margin-top: calc(var(--dialog-gap) * -1);
      position: sticky;
      bottom: gap-scale(1);
      padding: 0 gap-scale(1);
      height: 0;
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      z-index: 1;

      &:not(&--down):not(&--up) {
        display: none;
      }

      &:not(&--down) &__button--down {
        display: none;
      }

      &:not(&--up) &__button--up {
        display: none;
      }

      &__button {
        display: flex;
        padding: 8px;
        background-color: var(--color-text-alt);
        box-shadow: 0 0 8px 0 rgba(34, 60, 80, 0.2);
        border-radius: 50%;

        &::after {
          content: "";
          @extend %icon;
        }

        &--up {
          @include hoverType2;

          &::after {
            @extend %icon--arrow_upward;
          }
        }

        &--down {
          @include hoverType2;

          &::after {
            @extend %icon--arrow_downward;
          }
        }
      }
    }

    &__loader {
      position: sticky;
      bottom: 0;
      margin-top: calc(var(--dialog-gap) * -1);
      height: 0;
      display: flex;
      align-items: flex-end;
      z-index: 1;

      &:first-child {
        margin-top: var(--dialog-gap);
        height: auto;
      }

      &__inner {
        width: 100%;
        backdrop-filter: blur(1px);
        background: linear-gradient(180deg, color-mix(in srgb, var(--color-text-alt) 5%, #0000) 0%, var(--color-text-alt) 40%);
      }
    }

    &__unlock {
      display: flex;
      align-items: center;
      justify-content: center;
      @include hoverType2;
      gap: 8px;
      width: 100%;

      &::after {
        content: "";
        @extend %icon, %icon--edit;
      }
    }

    &__bottom {}
  }

  &__header {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    text-align: center;
    gap: gap-scale(0.5);

    @media (max-width: $mobile) {
      justify-content: space-between;
      text-align: left;
    }
  }

  &__burger {
    @include hoverType2;
    --icon-size: 32;

    @media (min-width: $mobile) {
      display: none;
    }

    &.is-active {
      &::after {
        @extend %icon--close;
      }
    }

    &::after {
      content: "";
      @extend %icon, %icon--menu;
    }
  }
}

#app {
  display: flex;
  flex: 1 1 0;
}