@use "icon" as *;

.loader {
	--loader-size: 56;
	--loader-color: var(--color-primary);
	--spinner-opacity: calc(var(--count) * 11);
	width: calc(var(--loader-size) * 1px);
	height: calc(var(--loader-size) * 1px);
	color: var(--loader-color);
	animation: spin 1s ease infinite both;
	display: flex;
	transform: rotate(calc(360deg / 12 * var(--count)));

	&::after {
		content: "";
		width: 100%;
		height: 100%;
		@extend %icon, %icon--spinner;
	}

	&--center {
		margin-left: auto;
		margin-right: auto;
	}

	svg {
		width: 100%;
		height: 100%;
	}
}

@keyframes spin {
	0% {
		--count: 0;
	}

	8.5% {
		--count: 1;
	}

	17% {
		--count: 2;
	}

	25.5% {
		--count: 3;
	}

	34% {
		--count: 4;
	}

	42.5% {
		--count: 5;
	}

	50% {
		--count: 6;
	}

	58.5% {
		--count: 7;
	}

	67% {
		--count: 8;
	}

	75.5% {
		--count: 9;
	}

	84% {
		--count: 10;
	}

	92.5% {
		--count: 11;
	}

	100% {
		--count: 12;
	}
}