@use "font" as *;
@use "mixins" as *;
@use "accordion" as *;

.app {
  &__header {
    box-shadow: 0 3px 4px color-mix(in srgb, var(--color-text) 25%, #0000);
    margin-bottom: gap-scale(1);
    position: sticky;
    background-color: var(--color-text-alt);
    z-index: 1;
    top: 0;

    &__inner {
      display: flex;
      align-items: center;
      padding: gap-scale(1) 0;
      gap: 12px;

      .logo {
        margin-right: auto;
        width: 130px;
        color: var(--color-primary);
      }
    }

    &__info {
      display: flex;
      align-items: center;
      gap: 4px;
    }

    &__menu {
      position: relative;
      display: flex;
      justify-content: flex-end;
      color: var(--color-primary);

      &__drop {
        position: absolute;
        top: calc(100% + 4px);
        flex-direction: column;
        gap: 8px;
        right: 0;
        left: unset;
        bottom: unset;
        background-color: var(--color-text-alt);
        padding: 12px;
        border: none;
        border-radius: var(--default-border-radius-small);
        width: max-content;
        box-shadow: 0 1px 5px color-mix(in srgb, var(--color-text) 50%, #0000);

        &:open {
          display: flex;
        }

        &__item {
          @include hoverType2;
          display: flex;
          gap: 4px;
          align-items: center;
        }
      }
    }
  }

  &__panel {
    position: sticky;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: var(--color-text-alt);
    padding: calc(var(--container-padding) / 2) var(--container-padding) var(--container-padding) var(--container-padding);
    // border-radius: var(--safe-area-inset-top) env(safe-area-inset-right, 20px) env(safe-area-inset-bottom, 20px) env(safe-area-inset-left, 20px);
    box-shadow: 0 3px 6px color-mix(in srgb, var(--color-text) 100%, #0000);
    // display: grid;
    // grid-auto-columns: 1fr;
    // grid-auto-flow: column;
    display: flex;
    justify-content: space-between;
    margin-top: 24px;

    &__item {
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;
      --icon-size: 40;
      @extend %font-2;
      @include hoverType2;

      &__caption {
        @media (max-width: 360px) {
          display: none;
        }
      }
    }
  }

  &__book {
    display: grid;
    gap: 20px;
    grid-template-columns: 130px 1fr;

    &--simple {
      display: grid;
      grid-template-columns: 32px 1fr;
      grid-auto-flow: row dense;
    }

    &--simple &__params {
      display: none;
    }

    &--simple &__action {
      --icon-size: 24;
    }

    &--simple &__cover {
      grid-row: 1 / 3;
      box-shadow: 0 1px 2px 2px color-mix(in srgb, var(--color-text) 15%, #0000);
    }

    &--simple &__title {
      grid-column: auto;
      @extend %font-3;
    }

    &__cover {
      box-shadow: 0 2px 4px 4px color-mix(in srgb, var(--color-text) 15%, #0000);
      align-self: start;
    }

    &__title {
      grid-column: 1 / -1;
      @include hoverType2;
      @extend %h4;
    }

    &__info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &__params {
      display: flex;
      flex-direction: column;
      gap: 4px;
      @extend %h6;

      &__item {

        dt,
        dd {
          display: inline;
        }

        dd {
          &::before {
            content: " ";
          }

          a {
            @include hoverType2;

            &:not(:last-child) span {
              &::after {
                content: ", ";
              }
            }
          }

          button {
            display: contents;
            @include hoverType2;

            &:not(:last-child) span {
              &::after {
                content: ", ";
              }
            }
          }
        }
      }
    }

    &__actions {
      align-self: flex-end;
      display: flex;
      gap: 12px;
    }

    &__action {
      --icon-size: 40;
      @include hoverType2;
    }

    &__footer {
      display: flex;
      align-items: center;
      gap: 12px;
      grid-column: 1 / -1;
    }

    &__date {
      display: flex;
      align-items: center;
      gap: 4px;
      flex-shrink: 0;

      &::before {
        content: "";
        @extend %icon, %icon--calendar;
      }
    }

    &__time {
      display: flex;
      align-items: center;
      gap: 4px;
      flex-shrink: 0;

      &::before {
        content: "";
        @extend %icon, %icon--clock;
      }
    }

    &__progress {
      width: 100%;
      border-radius: 666px;
      height: 10px;
      outline: 2px solid var(--color-primary);
      outline-offset: -2px;
      position: relative;
      background-image: linear-gradient(to right, var(--color-primary), var(--color-primary) calc(1% * var(--value)), #0000 calc(1% * var(--value)), #0000);
    }
  }

  &__list {
    &__item {
      &:not(:first-child) {
        padding-top: 16px;
        margin-top: 24px;
        border-top: 2px solid var(--default-border-color);
      }
    }
  }

  &__shelves {}

  &__shelves &__shelf {
    &:not(:first-child) {
      border-top: var(--default-border);
      padding-top: gap-scale(1);
      margin-top: gap-scale(1);
    }
  }

  &__shelf {
    &.is-active &__name::after {
      transform: scaleY(-1)
    }

    &--favorite &__name {
      &::before {
        @extend %icon, %icon--favorite;
      }
    }

    &__name {
      @extend %button-text, %font-3;

      &.is-active {
        &::after {
          transform: scaleY(-1);
        }
      }

      &::after {
        content: "";
        @extend %icon, %icon--chevron_down;
      }

      &::before {
        content: "";
        @extend %icon, %icon--newsstand;
      }
    }

    &__content {
      padding: 12px;
      margin-top: 12px;
    }

    &__item {
      &:not(:first-child) {
        margin-top: 12px;
        padding-top: 12px;
        border-top: 2px dashed var(--default-border-color);
      }
    }
  }

  &__filter {
    &__toggler {
      color: var(--color-primary);

      &.is-active {
        &::after {
          @extend %icon, %icon--close;
        }
      }

      &::after {
        content: "";
        @extend %icon, %icon--filter;
      }

      &.is-active {
        &::after {
          @extend %icon--close;
        }
      }
    }
  }
}